var instance = null
function QuestionsModel(){
}

QuestionsModel.prototype = {
	data : {},
	inject : function(prop, data){
		this.data[prop] = data;
	}
};
QuestionsModel.prototype.constructor = QuestionsModel;

function getInstance(){ 
	if(instance === null){
		return new QuestionsModel();
	} else {
		return instance;
	}
}

module.exports = getInstance;